img {
  display: block;
  width: 100%;
  height: auto;
}

section {
  margin: 0 auto;
}

.container {
  max-width: 375px;
  width: 100%;
  margin: 0 auto;
  padding-left: 24px;
  padding-right: 24px;

  @media (min-width: 376px) and (max-width: 575px) {
    max-width: 576px;
  }

  @media (min-width: $small-sm) {
    max-width: 576px;
  }

  @media (min-width: $medium-md) {
    max-width: 768px;
  }

  @media (min-width: $large-lg) {
    max-width: 992px;
  }

  @media (min-width: $extra-large-xl) {
    max-width: 1248px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

